html {
    font-size: 62.5%;
    box-sizing: border-box;
    
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $fuentePrincipal;
    font-size: 1.6rem;
    height:100%;
    margin:0;
    background-color: rgb(247, 227, 227);
}
p {
    font-size: 2rem;
    color: $negro;
}
.contenedor {
    max-width: 120rem;
    width: 95%;
    margin: 0 auto;
}
p {
    text-decoration: none;
}
img{
    max-width: 100%;
}
h1 {
    font-size: 4rem;
    font-weight: $bold;
    margin-top: 0;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}