.contenido-servicios{
    text-align: center;
    h3{
        font-weight: $delgada;
        font-size: $separacion;
    }
}
.galeria-servicios{
    
    margin: 0 0;
    padding: 0;
    list-style: none;
    margin-top: $separacion;

    @include smartwatch{
        img{
            width: 30rem;
            height: 30rem;
            border-radius: 50%;
            
        }
        @include grid (1,$separacion)
    }
    @include telefono{
        img{
            width: 40rem;
            height: 40rem;
            border-radius: 50%;
            padding: $separacion auto;
        }
        @include grid (1,$separacion)
    }
    @include tablet { // Tiene un bloque que varia
        img{
            width: 50rem;
            height: 50rem;
            border-radius: 50%;
        }
        @include grid (2,$separacion)
    }
    @include desktop {
        img{
            width: 60rem;
            height: 60rem;
            border-radius: 50%;
        }
        @include grid(2, $separacion)
    }
}
.img-1{
    background-image: url('https://res.cloudinary.com/dpikphrtb/image/upload/v1672785865/tratamiento-capilar-sevilla_hgfsfk.jpg');
    background-repeat: no-repeat;
    border-radius: 50%;
    background-size: cover;
    
    .texto-servicio{
        border-radius: 1rem;
        font-size: 5rem;
        background-color: rgb(255, 255, 255,0.2);
        font-weight: $delgada;
        transition: all 2s;
        padding: 0;
    }
    .texto-servicio:hover{
        font-size: 7rem;
        background-color: $blanco;
    }
}

   


